import React from 'react';
import ServicesVid from '../../videos/services-vid.mp4';
import Website from '../../images/web_serviuces.jpg';
import Mobile from '../../images/mobile_services.jpg'
import './ServicesPage.css'

function ServicesPage() {
    return (
        <section className="service-page-section">
            <div className="service-page-wrapper">
                <video autoPlay loop muted src={ServicesVid} type="video/mp4">
                </video>
                <h1 className="service-page-text">What We Provide</h1>
            </div>
            <div className="service-page-wrapper-2">
                <div className="service-page-inner-wrapper">
                    <div className="service-page-inner-text">
                        <h1 className="service-page-top-text">
                            Services
                        </h1>
                        <p className="service-page-down-text">
                            At ShanDots we offer professional Web Apps and Mobile Apps services at affordable rates to help your business attract more visitors and keep them on your site!
                        </p>
                    </div>
                </div>
            </div>
            <div className="service-page-wrapper-2">
                <div className="service-page-inner-wrapper-2">
                    <div className="service-page-container">
                        <div className="service-page-info">
                            <div className="service-info-text">
                                <h1 className="service-info-top-text">Web Development</h1>
                                <p className="service-info-down-text">
                                As the world is moving towards an online platform, it is necessary for a company to have its own website. We have expertise in developing
                                websites with various features as per customer needs.
                                </p>
                                <button className="service-info-button">View Projects</button>
                            </div>
                        </div>
                        <div className="service-page-image">
                            <div className="service-info-text">
                                <img  className="service-image" src={Website} />
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div className="service-page-wrapper-2">
                <div className="service-page-inner-wrapper-2">
                    <div className="service-page-container">
                        <div className="service-page-image">
                            <div className="service-info-text">
                                <img  className="service-image" src={Mobile} />
                            </div>
                        </div>
                        <div className="service-page-info">
                            <div className="service-info-text">
                                <h1 className="service-info-top-text">Mobile App Development</h1>
                                <p className="service-info-down-text">With the Smartphone technology getting a boost, Mobile Apps are the need of the hour.
                                We are specialized in developing Cross-Platform Mobile Apps.
                                </p>
                                <button className="service-info-button">View Projects</button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    )
}

export default ServicesPage
