import React from 'react'
import './Contact.css'

function Contact() {
    return (
        <div>
            <section className="contact-section">
                <div className="contact-section-header">
                    <div className="contact-section-container">
                        <h2 className="contact-section-title">
                            Want To Discuss Some Ideas?
                        </h2>
                        <p className="contact-section-text">
                            Share With Us Your Contact Details And Our Team Will Get In Touch With You Soon.
                        </p>
                        <form className="contact-section-form">
                            <input className="form-text" type="text" name="username" placeholder="Name" required></input>
                            <input className="form-text" type="email" name="email" placeholder="Email" required></input>
                            <button className="button-contact">Contact</button>
                        </form>
                    </div>

                        
                </div>

            </section>
        </div>
    )
}

export default Contact
