import React from 'react';
import './Tech.css';
import TechVid from '../../videos/tech-vid.mp4';
import HTML5 from '../../images/html5.png';
import CSS3 from '../../images/css3a.png';
import JS from '../../images/js.png';
import REACT from '../../images/react.png';
import NODEJS from '../../images/nodejslogo.png';
import ANDROID from '../../images/android.png';
import IOS from '../../images/ios.png';
import MONGO from '../../images/mongodb.png';
import MYSQL from '../../images/mysql.png';
import SQL from '../../images/sql.png';


function Tech() {
    return (
        <section className="tech-section">
                <div className="tech-wrapper">
                    <video autoPlay loop muted src={TechVid} type="video/mp4">
                    </video>
                    <h1 className="vid-text">Technologies</h1>
                </div>
                <div className="tech-wrapper-2">
                    <div className="tech-inner-wrapper">
                        <div className="tech-inner-text">
                            <h1 className="tech-top-text">
                                Web
                            </h1>
                            <p className="tech-down-text">
                            With the ever-growing demand for moving business to a digital platform, the need for Web Apps has increased dramatically in the past years. As technology keeps upgrading, we need to get accustomed to the latest technology and we at ShanDots strive to achieve this. At ShanDots, we use the latest technology to build world-class Web Apps with great importance to User-Friendly UI, Security, and Data Handling. We have successfully deployed Web Apps for customers across the world with these technologies
                            </p>
                        </div>
                    </div>
                </div>
                <div className="tech-wrapper-2">
                    <div className="tech-inner-wrapper2">
                        <div className="tech-inner-text2">
                            <div className="tech-box">
                                <div className="img-sec">
                                    <img className="img-sec-inner" src={HTML5} alt="html5"></img>
                                    <h3 className="tech-text-center">HTML 5</h3>
                                    <p className="tech-para">HTML5 is a markup language used for structuring and presenting content on the World Wide Web. It is the fifth and last major HTML version that is a World Wide Web Consortium recommendation.</p>
                                </div>

                            </div>
                            <div className="tech-box">
                                <div className="img-sec">
                                    <img className="img-sec-inner" src={CSS3} alt="css3"></img>
                                    <h3 className="tech-text-center">CSS 3</h3>
                                    <p className="tech-para">Cascading Style Sheets is a style sheet language used for describing the presentation of a document written in a markup language such as HTML. CSS is a cornerstone technology of the World Wide Web, alongside HTML and JavaScript.</p>
                                </div>
                            </div>
                            <div className="tech-box">
                                <div className="img-sec">
                                    <img className="img-sec-inner" src={JS} alt="javascript"></img>
                                    <h3 className="tech-text-center">Javacript</h3>
                                    <p className="tech-para">JavaScript is the dominant client-side scripting language of the Web, with 95% of websites using it for this purpose. Scripts are embedded in or included from HTML documents and interact with the DOM.</p>
                                </div>
                                
                            </div>
                            <div className="tech-box">
                                <div className="img-sec">
                                    <img className="img-sec-inner" src={REACT} alt="react"></img>
                                    <h3 className="tech-text-center">Reactjs</h3>
                                    <p className="tech-para">React is an open-source, front end, JavaScript library for building user interfaces or UI components. It is maintained by Facebook and a community of individual developers and companies. React can be used as a base in the development of single-page or mobile applications.</p>
                                </div>
                            </div>
                            <div className="tech-box">
                                <div className="img-sec">
                                    <img className="img-sec1-inner" src={NODEJS} alt="react"></img>
                                    <h3 className="tech-text-center">Nodejs</h3>
                                    <p className="tech-para">Node.js is an open-source, cross-platform, back-end JavaScript runtime environment that runs on the Chrome V8 engine and executes JavaScript code outside a web browser.</p>
                                </div>
                            </div>
                    
                        </div>
                    </div>
                </div>
                <div className="tech-wrapper-2">
                    <div className="tech-inner-wrapper">
                        <div className="tech-inner-text">
                            <h1 className="tech-top-text">
                                Mobile
                            </h1>
                            <p className="tech-down-text">
                            As Smartphone technology has rapidly grown, the need for the application to expand your business is the need of an hour. As years have passed, the technologies to develop these mobile apps have gone through constant evolution and we at ShanDots use the latest technologies to build these Mobile Apps. We have successfully developed Mobile Apps for customers across the world with these technologies.
                            </p>
                        </div>
                    </div>
                </div>
                <div className="tech-wrapper-2">
                    <div className="tech-inner-wrapper2">
                        <div className="tech-inner-text2">
                            <div className="tech-box">
                                <div className="img-sec">
                                    <img className="img-sec-inner" src={ANDROID} alt="android"></img>
                                    <h3 className="tech-text-center">Android</h3>
                                    <p className="tech-para">Android is mobile app development technology used to build mobile apps running on Android Operating systems with the help of Android Software Development Kit.</p>
                                </div>

                            </div>
                            <div className="tech-box">
                                <div className="img-sec">
                                    <img className="img-sec-inner" src={IOS} alt="ios"></img>
                                    <h3 className="tech-text-center">IOS</h3>
                                    <p className="tech-para">iOS is primarily used to build apps for iOS devices. It uses the tools provided in the iOS developer kit for app development.</p>
                                </div>
                                
                            </div>
                            <div className="tech-box">
                                <div className="img-sec">
                                    <img className="img-sec-inner" src={REACT} alt="react-native"></img>
                                    <h3 className="tech-text-center">React-Native</h3>
                                    <p className="tech-para">React Native is an open-source mobile application framework created by Facebook, Inc. It is used to develop applications for Android, Android TV, iOS, macOS, tvOS, Web, Windows and UWP by enabling developers to use React's framework along with native platform capabilities.</p>
                                </div>
                                
                            </div>
                    
                        </div>
                    </div>
                </div>
                <div className="tech-wrapper-2">
                    <div className="tech-inner-wrapper">
                        <div className="tech-inner-text">
                            <h1 className="tech-top-text">
                                Database
                            </h1>
                            <p className="tech-down-text">
                            As the usage of Mobile Apps and Web Apps have increased, handling a large amount of data is crucial. We at ShanDots use the latest database to store, search and update your data with great ease.
                            </p>
                        </div>
                    </div>
                </div>
                <div className="tech-wrapper-2">
                    <div className="tech-inner-wrapper2">
                        <div className="tech-inner-text2">
                            <div className="tech-box">
                                <div className="img-sec">
                                    <img className="img-sec-inner" src={MONGO} alt="mongodb"></img>
                                    <h3 className="tech-text-center">Mongodb</h3>
                                    <p className="tech-para">MongoDB is a source-available cross-platform document-oriented database program. It stores data in JSON-like formats.</p>
                                </div>

                            </div>
                            <div className="tech-box">
                                <div className="img-sec">
                                    <img className="img-sec-inner" src={MYSQL} alt="mysql"></img>
                                    <h3 className="tech-text-center">Mysql</h3>
                                    <p className="tech-para">MySQL Database Service is a fully managed database service that lets developers quickly develop and deploy secure, cloud native applications using the world’s most popular open source database. </p>
                                </div>
                                
                            </div>
                            <div className="tech-box">
                                <div className="img-sec">
                                    <img className="img-sec2-inner" src={SQL} alt="sql-server"></img>
                                    <h3 className="tech-text-center">Sql Server</h3>
                                    <p className="tech-para">Microsoft SQL Server is a relational database management system developed by Microsoft. As a database server, it is a software product with the primary function of storing and retrieving data as requested by other software applications</p>
                                </div>
                                
                            </div>
                            
                    
                        </div>
                    </div>
                </div>
        </section>
    )
}

export default Tech
