import React from 'react'
import {FaBars, FaBorderNone} from 'react-icons/fa';
import Carousel from "react-multi-carousel";
import "react-multi-carousel/lib/styles.css";
import Payment from '../../images/secured_payment.jpg';
import WebsiteDev from '../../images/website-development.jpg';
import Ecommerce from '../../images/ecommerce.jpg';
import MobileApp from '../../images/mobile-apps.jpg';
import logo from '../../images/logo3.png';
import './Hero3.css';

function Hero3({toggle}) {

    const responsive = {
        superLargeDesktop: {
          breakpoint: { max: 4500, min: 3000 },
          items: 3
        },
        desktop: {
          breakpoint: { max: 3000, min: 1024 },
          items: 3
        },
        tablet: {
          breakpoint: { max: 1024, min: 700 },
          items: 2
        },
        mobile: {
          breakpoint: { max: 700, min: 0 },
          items: 1
        }
      };

    return (
        <div>
            <nav>
                <div>
                    <a  href='/' className="imgn"><img className='imgn' src={logo} /></a>
                </div>

                <div className="mobileIcon" onClick={toggle}>
                <FaBars />
                </div>

                <ul className="nav-list">
                    <li><a href="/aboutus">about us</a></li>
                    <li><a href="/services">services</a></li>
                    <li><a href="/technologies">technologies</a></li>
                    <li><a href="/products">products</a></li>
                    <li><a href="/contact">contact</a></li>
                </ul>

            </nav>
            
            <section className="sec1">
                <div className="sec1-text">
                    <h3 className="sec1-heading">&lt;Shan<span>D</span>ots..../&gt;</h3>
                    <Carousel 
                        showDots={false}
                        responsive={responsive}
                        infinite={true}
                        autoPlay={true}
                        autoPlaySpeed={2500}
                        keyBoardControl={true}
                        arrows={false}
                        className="sec1-carousel"
                    >
                        <div className="sec1-card">
                            <div className="sec1-card-overlay">
                                <div className="sec1-card-text">Web Development</div>
                            </div>
                            <img src={WebsiteDev}/>
                        </div>
                        <div className="sec1-card">
                            <div className="sec1-card-overlay">
                                <div className="sec1-card-text">E-commerce Development</div>
                            </div>
                            <img src={Ecommerce}/>
                        </div>
                        <div className="sec1-card">
                            <div className="sec1-card-overlay">
                                <div className="sec1-card-text">Mobile Apps Development</div>
                            </div>
                            <img src={MobileApp}/>
                        </div>
                        <div className="sec1-card">
                            <div className="sec1-card-overlay">
                                <div className="sec1-card-text">Application Security</div>
                            </div>
                            <img src={Payment}/>
                        </div>
                        {/* <div className="sec1-card">5</div>
                        <div className="sec1-card">6</div>
                        <div className="sec1-card">7</div> */}
                    </Carousel>
                </div>
            </section>
        </div>
        
    )
}

export default Hero3
