import React from 'react'
import ProdVid from '../../videos/products-vid.mp4';
import './Products.css';

function Products() {
    return (
        <section className="product-section">
            <div className="product-wrapper">
                    <video autoPlay loop muted src={ProdVid} type="video/mp4">
                    </video>
                    <h1 className="product-text">Products</h1>
                </div>
            
        </section>
    )
}

export default Products
