import React,{useState,useEffect} from 'react';
import {makeStyles} from '@material-ui/core/styles';
import {FaBars, FaBorderNone} from 'react-icons/fa';
import logo from '../../images/logo2.jpg';
import Carousel from "react-multi-carousel";
import "react-multi-carousel/lib/styles.css";
import bg from '../../images/bg7.jpg';
import './Hero2.css';

const useStyles=makeStyles((theme) => ({
  root: {
      flexGrow :1,
  },
  menuButton: {
      marginRight: theme.spacing(2),
      color:'black'
  },
  title: {
      flexGrow: 1,
      color: 'black',
      text:'shandots'
  },
  appBarTransparent: {
      // backgroundColor: 'rgba(f,f,f,0.5)'
      
      // backgroundColor:'#051444'
  },
  appBarSolid:{
      // backgroundColor: 'rgba(1,191,113)'
      // backgroundColor: 'rgba(f,f,f,0.5)'
      backgroundColor: '#abb0b1'
  }
  
}));

function Hero2({toggle}) {

    const responsive = {
        superLargeDesktop: {
          breakpoint: { max: 4500, min: 3000 },
          items: 3
        },
        desktop: {
          breakpoint: { max: 3000, min: 1024 },
          items: 3
        },
        tablet: {
          breakpoint: { max: 1024, min: 500 },
          items: 2
        },
        mobile: {
          breakpoint: { max: 500, min: 0 },
          items: 1
        }
      };

      const classes=useStyles();
    const [navBackground, setNavBackground] = useState('appBarTransparent')

    const navRef = React.useRef()
    navRef.current=navBackground

    useEffect(() => {
        const handleScroll = () => {
            const show = window.scrollY > 410
            if (show) {
                setNavBackground('appBarSolid')
            }
            else{
                setNavBackground('appBarTransparent')
            }
        }
        document.addEventListener('scroll',handleScroll)
        return () => {
            document.removeEventListener('scroll',handleScroll)
        }
    }, [])

    return (
        
            <section className="Hero-section">
              
                <img className="hero-img" src={bg} />
                
                
                {/* <div className="Hero-section-wrapper"> */}
                  
                    
                    <div className="hero-text-bottom">
                        {/* <p className="TopLine">WEB DEVELOPMENT</p>
                        <p className="TopLine">APP DEVELOPMENT</p>   */}
                        <h3 className="text-footer">&lt;Shan<span>D</span>ots..../&gt;</h3>
                        <Carousel 
                            showDots={false}
                            responsive={responsive}
                            infinite={true}
                            autoPlay={true}
                            autoPlaySpeed={2500}
                            keyBoardControl={true}
                            arrows={false}
                        >
                            <div className="hero-card">1</div>
                            <div className="hero-card">2</div>
                            <div className="hero-card">3</div>
                            <div className="hero-card">4</div>
                            <div className="hero-card">5</div>
                            <div className="hero-card">6</div>
                            <div className="hero-card">7</div>
                        </Carousel>
                    </div>
                {/* </div> */}
            </section>
            
        
    )
}

export default Hero2
