import React from 'react'
import './Customers.css';
import client1 from '../../images/clients-1.png';
import client2 from '../../images/clients-2.png';
import client3 from '../../images/clients-3.png';
import client4 from '../../images/clients-4.png';
import client5 from '../../images/clients-5.png';
import client6 from '../../images/clients-6.png';


function Customers() {
    return (
        <div>
            <div className="CustContainer">
                <div className="CustWrapper">
                    <div className="CustRow">
                        <div className="CustCol">
                            <div className="CustText">
                                {/* <span>We Are Creative Agency</span> */}
                                <h3 className="bigtext-cust">Some of our Clients</h3>
                            </div>
                        </div>
                        
                    </div>
                    <div className="CustRow2">
                        <div className="CustBox"><img src={client1} /></div>
                        <div className="CustBox"><img src={client2} /></div>
                        <div className="CustBox"><img src={client3} /></div>
                        <div className="CustBox"><img src={client4} /></div>
                        <div className="CustBox"><img src={client5} /></div>
                        <div className="CustBox"><img src={client6} /></div>
                        {/* <div className="CustBox"><img src={client1} /></div>
                        <div className="CustBox"><img src={client2} /></div>
                        <div className="CustBox"><img src={client3} /></div>
                        <div className="CustBox"><img src={client4} /></div>
                        <div className="CustBox"><img src={client5} /></div>
                        <div className="CustBox"><img src={client6} /></div>     */}
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Customers
