import styled from 'styled-components'
// import { Link as LinkR } from 'react-router-dom'
import { Link as LinkS } from 'react-scroll';

export const Links= styled(LinkS)`
    color:#000;
    display:flex;
    align-items:center;
    text-decoration:none;
    padding:0 1rem;
    height:100%;
    cursor:pointer;

    &.active {
        border-bottom: 3px solid #01bf71;
    }
`;