import React from 'react'
import './Process.css';
import Gathering from '../../../images/gathering.png';
import Design from '../../../images/design.PNG';
import Develop from '../../../images/develop.png';
import Launch from '../../../images/launch.png'

function Process() {
    return (
        <section className="ps-timeline-sec">
            <div className="timeline-wrapper-2">
                <div className="timeline-inner-wrapper2">
                    <div className="timeline-inner-text">
                        <h1 className="timeline-top-text-2">How It Is Going</h1>
                    </div>
                </div>
            </div>
        <div className="timeline-container">
            <ol className="ps-timeline">
                <li>
                    <div className="img-handler-top">
                        <img className="timeline-img" src={Gathering} alt=""/>
                    </div>
                    <div className="ps-bot">
                        <p>Step 1:- Information Gathering and Planning.</p>
                    </div>
                    <span className="ps-sp-top">01</span>
                </li>
                <li>
                    <div className="img-handler-bot">
                        <img className="timeline-img" src={Design} alt=""/>
                    </div>
                    <div className="ps-top">
                        <p>Step 2:- Design- Construct according to the customers need.</p>
                    </div>
                    <span className="ps-sp-bot">02</span>
                </li>
                <li>
                    <div className="img-handler-top">
                        <img className="timeline-img" src={Develop} alt=""/>
                    </div>
                    <div className="ps-bot">
                        <p>Step 3:- Develop the project.</p>
                    </div>
                    <span className="ps-sp-top">03</span>
                </li>
                <li>
                    <div className="img-handler-bot">
                        <img className="timeline-img" src={Launch} alt=""/>
                    </div>
                    <div className="ps-top">
                        <p>Step 4:- Test and Launch the Project.</p>
                    </div>
                    <span className="ps-sp-bot">04</span>
                </li>
                
            </ol>
        </div>
    </section>
    )
}

export default Process
