import React,{useState} from 'react'
import Sidebar from '../Components/Sidebar/Sidebar';
import Footer from '../Components/Footer/Footer';
import Customers from '../Components/Customers/Customers';
import Work from '../Components/Work/Work';
// import Hero1 from '../Components/HeroSection/Hero1';
import Count from '../Components/AboutUs/Count';
import Expertise from '../Components/Expertise/Expertise';
import Projects from '../Components/Projects/Projects';
import Contact from '../Components/Contact/Contact';
import { Route } from 'react-router-dom';
import Hero2 from '../Components/HeroSection/Hero2';
import ContactUs from '../Components/ContactUs/ContactUs';
import Hero3 from '../Components/HeroSection/Hero3';



function Home() {
    const [isOpen, setIsOpen]= useState(false);

    const toggle = () => {
        setIsOpen(!isOpen);
    };

   

   
    return (
        <>
            <Sidebar isOpen={isOpen} toggle={toggle} />
            {/* <Header1 toggle={toggle}/> */}
            {/* <Hero2 /> */}
            <Hero3 toggle={toggle} />
            {/* {data.counts.map(count => <Count key={count.id} data={count}/>)} */}
            {/* <AboutNew /> */}
            <Count />
            <Projects />
            {/* <Expertise /> */}
            <Work />
            <Customers />
            <ContactUs />
            
            {/* <Footer /> */}
            

            
               
          
            
            
        </>
    )
}

export default Home
