import React from 'react'
import './Work.css';
import image1 from '../../images/image-d.jpg';
import image2 from '../../images/image-1.jpg';
import image3 from '../../images/image-6.jpg';
import image4 from '../../images/image-c.jpg';

function Work() {
    return (
        <div>
            <div className="WorkContainer">
                <div className="WorkWrapper">
                    <div className="WorkRow">
                        <div className="Work-Column">
                            <div className="WorkText">
                                <h3 className="bigtext">Our Latest Creative Work....</h3>
                            </div>
                        </div>
                        
                    </div>
                    <div className="WorkRow2">
                        <div className="WorkBox1"><img className="imgw" src={image1} /></div> 
                        <div className="WorkBox2"><img className="imgw" src={image2} /></div>
                        <div className="WorkBox2"><img className="imgw" src={image3} /></div> 
                        {/* <div className="WorkBox1"><img className="imgw" src={image4} /></div>   */}
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Work
